
@import url('https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,200;0,400;1,300&display=swap');

.memorygame_title {
  margin: 4rem 0 4rem 0;
  color: #5CBE7D;
  text-align: center;
  font-family: "Raleway", sans-serif;
}

.sub_header {
  padding: 5px;
  color: #5CBE7D;
  font-size: 20px;
  font-weight: 800;
  justify-content: center;
  display: flex;
  flex-direction: column;
}



.number_turn {
  text-transform: uppercase;
  /* margin-left: 3rem; */
}

.moves {
  margin-bottom: 1rem;
}

.memory_text_best_score {
  margin-bottom: 2rem;
  /* margin-right: 3rem; */
  
}

.memorygame_body {
  background: white;
  height: 100%;
  text-align: center;
}

.header_card_button {
  position: relative;
  padding: 1rem 3rem;
  height: auto;
  border-radius: 10px;
  overflow: hidden;
  cursor: pointer;
  font-size: 2rem;
  

}


.marquee {
  position:absolute;
  z-index: 10;
  white-space: nowrap;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: flex;
  
}

.marquee span {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-shrink: 0;
  width: 100%;
  height: 100%;
  background-color: #5CBE7D;
}

.header_card_button:hover .memory_base_txt {
  opacity: 0;
}

.header_card_button:hover .marquee {
  animation: translateInfinite 1.3s linear infinite;
  font-style: italic;

}

@keyframes translateInfinite {
  100% {
    transform: translateX(-100%);
  }
}


.btn-group-vertical {
  width: auto;
  margin-bottom: 1rem;
}

.memory_card {
  width: 100%;
  height: 100px;
  border-radius: 4px;
  box-shadow: 2px 2px 4px 4px #c1d9dde5;
  transition: 0.5s;
  transform-style: preserve-3d;
  position: relative;
  cursor: pointer;
}

.memorycard_container {
  padding: 0 2rem 4rem 2rem;
}

.header_container {
  margin: 2rem 0;
}

.memory_card_img {
  width: 100%;
  height: 100%;
  box-shadow: 2px 2px 4px 4px #c1d9dde5;
  border-radius: 4px;
}

.is_flipped {
  transform: rotateY(180deg);
}

.card_face {
  backface-visibility: hidden;
  position: absolute;
  width: 100%;
  height: 100%;
}
.card_back_face {
  transform: rotateY(180deg);
  width: 100%;
  height: 100%;
}

.is_inactive {
  /* visibility: hidden; */
  opacity: 0;
}

.memory_game_hr {
  
  width: 80%;
  height: 0px;
  border: none;
  border-top: 4px solid black;
  margin: 0 auto;
}



@media (min-width: 768px) {
  

  .memorygame_body { 
    height: 100%;
    text-align: center;
    padding-bottom: 2rem;
  }

  .header_container{
    display: flex;
    justify-content: space-evenly;
    flex-direction: row;
    flex-wrap: wrap;
  }
  

  .memorycard_container {
    max-height: 100vh;
  }

  .number_container {
    display: flex;
    flex-direction: row;
    
    
  }

  .number_turn {
    margin-left: 2rem;
  }

  .memory_card {
    height: 150px;
  }

  .memorycard_container {
    margin: 0 5rem;
  }

}

@media (min-width: 1000px) {

  .memory_card {
    height: 170px;
    width: 80%;
  }

  .memorycard_container {
    width: 70%;
    margin: 0 auto;
  }

  .header_card_button {
    margin-right: 10vw;
  }
  
  .btn-group-vertical {
    margin-left: 8vw;
  }
} 
