 .flip-opacity{
  -webkit-animation: opacity 6s ;
          animation: opacity 6s ;
 }

 .flip-container {
    -webkit-perspective: 800px;
            perspective: 800px;
    width: 300px;
    height: 300px;
    position: absolute;
    top: 50px;
    right: 50px;
    -webkit-animation: move 6s 1s cubic-bezier(.6,0,.4,1) infinite;
            animation: move 6s 1s cubic-bezier(.6,0,.4,1) infinite;
  }
  
  .flip-container:hover .flipper {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
  }
  
  .flipper {
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
    position: absolute;
    height: 100%;
    width: 100%;
    -webkit-animation: rotate 6s 1s cubic-bezier(.6,0,.4,1) infinite;
            animation: rotate 6s 1s cubic-bezier(.6,0,.4,1) infinite;
  }
  
  @-webkit-keyframes opacity{
    0% {
      opacity: 0;
    }
    100% {
     opacity: 1;
    }
  }
  
  @keyframes opacity{
    0% {
      opacity: 0;
    }
    100% {
     opacity: 1;
    }
  }
  
  @-webkit-keyframes rotate {
    0% {-webkit-transform: rotateX(0deg)
    rotateY(0deg) rotateZ(0deg);transform: rotateX(0deg)
    rotateY(0deg) rotateZ(0deg)}
    30% {-webkit-transform: rotateX(0deg)
    rotateY(0deg) rotateZ(-5deg);transform: rotateX(0deg)
    rotateY(0deg) rotateZ(-5deg)}
    60%, 80% {-webkit-transform: rotateX(360deg)
    rotateY(180deg) rotateZ(-20deg);transform: rotateX(360deg)
    rotateY(180deg) rotateZ(-20deg)}
    100% {-webkit-transform: rotateX(0deg)
    rotateY(180deg) rotateZ(0deg);transform: rotateX(0deg)
    rotateY(180deg) rotateZ(0deg)}
  }
  
  @keyframes rotate {
    0% {-webkit-transform: rotateX(0deg)
    rotateY(0deg) rotateZ(0deg);transform: rotateX(0deg)
    rotateY(0deg) rotateZ(0deg)}
    30% {-webkit-transform: rotateX(0deg)
    rotateY(0deg) rotateZ(-5deg);transform: rotateX(0deg)
    rotateY(0deg) rotateZ(-5deg)}
    60%, 80% {-webkit-transform: rotateX(360deg)
    rotateY(180deg) rotateZ(-20deg);transform: rotateX(360deg)
    rotateY(180deg) rotateZ(-20deg)}
    100% {-webkit-transform: rotateX(0deg)
    rotateY(180deg) rotateZ(0deg);transform: rotateX(0deg)
    rotateY(180deg) rotateZ(0deg)}
  }
  
  @-webkit-keyframes move {
    0% {top: 50px; right: 50px; -webkit-transform: rotate(0deg); transform: rotate(0deg)}
    20% {top: calc(50% - 200px); right: calc(50% - 200px); width: 400px; height: 400px; -webkit-transform: rotate(0deg); transform: rotate(0deg)}
    80% {top: calc(50% - 200px); right: calc(50% - 200px); width: 400px; height: 400px; -webkit-transform: rotate(-15deg); transform: rotate(-15deg)}
    100% {top: 50px; right: 50px; -webkit-transform: rotate(0deg); transform: rotate(0deg)}
  }
  
  @keyframes move {
    0% {top: 50px; right: 50px; -webkit-transform: rotate(0deg); transform: rotate(0deg)}
    20% {top: calc(50% - 200px); right: calc(50% - 200px); width: 400px; height: 400px; -webkit-transform: rotate(0deg); transform: rotate(0deg)}
    80% {top: calc(50% - 200px); right: calc(50% - 200px); width: 400px; height: 400px; -webkit-transform: rotate(-15deg); transform: rotate(-15deg)}
    100% {top: 50px; right: 50px; -webkit-transform: rotate(0deg); transform: rotate(0deg)}
  }
  
  .front,
  .back {
    -webkit-backface-visibility: hidden;
